/**
 * Transform an object params in URLParameters
 * @param params - the object to transform
 * @returns
 */
export function uriFy(params: any): string {
  return Object.keys(params)
    .map((k: string) => {
      if (Array.isArray(params[k])) {
        const filterParams = params[k]
          .map((subData: string) => encodeURIComponent(subData))
          .join(',')
        return encodeURIComponent(k) + '=' + filterParams
      } else if (['string', 'number', 'boolean'].includes(typeof params[k])) {
        const value =
          typeof params[k] === 'boolean' ? String(params[k]) : params[k]
        return encodeURIComponent(k) + '=' + encodeURIComponent(value)
      }

      return ''
    })
    .join('&')
}
